import parser from 'postcss-selector-parser';

export function nodesAreEquallySpecific(nodes) {
	// Selector specificity is important when the parent selector is a list.
	// These cases should be resolved with `:is()` pseudo.
	// Since browser support for `:is()` is not great, we try to avoid it.
	// If the selector specificity is equal for all items in the selector list, we don't need `:is`.

	const specificities = nodes.map((node) => {
		return parser().astSync(node);
	}).map((ast) => {
		return selectorSpecificity(ast);
	});

	const first = specificities[0];
	for (let i = 1; i < specificities.length; i++) {
		if (first.a === specificities[i].a && first.b === specificities[i].b && first.c === specificities[i].c) {
			continue;
		}

		return false;
	}

	return true;
}

export function selectorSpecificity(node) {
	let a = 0;
	let b = 0;
	let c = 0;

	if (node.type === 'id') {
		a += 1;
	} else if (node.type === 'tag') {
		c += 1;
	} else if (node.type === 'class') {
		b += 1;
	} else if (node.type === 'attribute') {
		b += 1;
	} else if (node.type === 'pseudo') {
		switch (node.value) {
			case '::after':
			case ':after':
			case '::backdrop':
			case '::before':
			case ':before':
			case '::cue':
			case '::cue-region':
			case '::first-letter':
			case ':first-letter':
			case '::first-line':
			case ':first-line':
			case '::file-selector-button':
			case '::grammar-error':
			case '::marker':
			case '::part':
			case '::placeholder':
			case '::selection':
			case '::slotted':
			case '::spelling-error':
			case '::target-text':
				c += 1;
				break;

			case ':is':
			case ':has':
			case ':not':
				{
					const pseudoSpecificity = selectorSpecificity(node.nodes[0]);
					a += pseudoSpecificity.a;
					b += pseudoSpecificity.b;
					c += pseudoSpecificity.c;
					break;
				}

			case 'where':
				break;

			case ':nth-child':
			case ':nth-last-child':
				{
					const ofSeparatorIndex = node.nodes.findIndex((x) => {
						x.value === 'of';
					});

					if (ofSeparatorIndex > -1) {
						const ofSpecificity = selectorSpecificity(parser.selector({ nodes: node.nodes.slice(ofSeparatorIndex + 1) }));
						a += ofSpecificity.a;
						b += ofSpecificity.b;
						c += ofSpecificity.c;
					} else {
						a += a;
						b += b;
						c += c;
					}
				}
				break;

			default:
				b += 1;
		}
	} else if (node.nodes && node.nodes.length > 0) {
		node.nodes.forEach((child) => {
			const specificity = selectorSpecificity(child);
			a += specificity.a;
			b += specificity.b;
			c += specificity.c;
		});
	}

	return {
		a,
		b,
		c,
	};
}

